.page {
  margin: 20px auto;
  min-height: 90%;
  width: 80%;
  max-width: 1200px;
  padding-bottom: 50px;
}

.page-header > h2 {
  font-size: 25px;
  font-weight: 400;
  color: var(--black);
  line-height: 1.7;
}

.page-header-title {
  margin-top: 50px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
}

.page-header-title > h1 {
  font-size: 60px;
  margin-right: 50px;
  max-width: 80%;
  flex-shrink: 0;
}

.page-header-line {
  width: 100%;
  height: 5px;
  background-color: var(--blue);
  border-radius: 10px;
}

.page-content {
  position: relative;
  margin: auto;
  width: 100%;
  margin-top: 50px;
}

@media only screen and (max-width: 800px) {
  .page {
    width: 100%;
    padding: 20px;
    box-sizing: border-box;
    max-width: 100vw;
  }

  .page-header-title {
    align-items: flex-start;
    flex-direction: column;
  }

  .page-header-line {
    width: 80%;
    background-color: var(--blue);
  }

  .page-header-title > h1 {
    font-size: 50px;
    margin-right: 0px;
  }
}

@media only screen and (max-width: 600px) {
  .page-header-line {
    width: 90%;
    margin-top: 10px;
  }

  .page-header-title > h1 {
    font-size: 40px;
    margin-right: 0px;
  }
}
